}
.navbar{display:none
}
.badge{border:1px solid #000
}
.table{border-collapse:collapse !important
}
.table td,.table th{background-color:#fff !important
}
.table-bordered th,.table-bordered td{border:1px solid #dee2e6 !important
}
.table-dark{color:inherit
}
.table-dark th,.table-dark td,.table-dark thead th,.table-dark tbody+tbody{border-color:#dee2e6
}
.table .thead-dark th{color:inherit;border-color:#dee2e6
}
}
body{font-size:.875rem
}
.feather{width:16px;height:16px;vertical-align:text-bottom
}
.sidebar{position:fixed;top:0;bottom:0;left:0;z-index:100;padding:48px 0 0;-webkit-box-shadow:inset -1px 0 0 rgba(0,0,0,.1);box-shadow:inset -1px 0 0 rgba(0,0,0,.1)
}
@media(max-width: 767.98px){
.sidebar{top:5rem
}
}
.sidebar-sticky{position:relative;top:0;height:calc(100vh - 48px);padding-top:.5rem;overflow-x:hidden;overflow-y:auto
}
.sidebar .nav-link{font-weight:500;color:#333
}
.sidebar .nav-link .feather{margin-right:4px;color:#727272
}
.sidebar .nav-link.active{color:#2470dc
}
.sidebar .nav-link:hover .feather,.sidebar .nav-link.active .feather{color:inherit
}
.sidebar-heading{font-size:.75rem;text-transform:uppercase
}
.navbar-brand{padding-top:.75rem;padding-bottom:.75rem;font-size:1rem;background-color:rgba(0,0,0,.25);-webkit-box-shadow:inset -1px 0 0 rgba(0,0,0,.25);box-shadow:inset -1px 0 0 rgba(0,0,0,.25)
}
.navbar .navbar-toggler{top:.25rem;right:1rem
}
.navbar .form-control{padding:.75rem 1rem;border-width:0;border-radius:0
}
.form-control-dark{color:#fff;background-color:rgba(255,255,255,.1);border-color:rgba(255,255,255,.1)
}
.form-control-dark:focus{border-color:transparent;-webkit-box-shadow:0 0 0 3px rgba(255,255,255,.25);box-shadow:0 0 0 3px rgba(255,255,255,.25)
}
.GroupMemberInside{
  width: 100%;
  border-radius: 50%;
}
.jumbotron-bg{
  background-color: #190e1b;
  min-height: 400px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 0px;
  position: relative;
}
.SingerBg{
  position: absolute; 
  top: 0; 
  bottom:0;   
  background-image: url(../../static/img/singlebird.4c38437.gif);
} 

